<template>
   
    <div id='saiy'>  
     <son :mydata='gg' @father1="add()"></son>
        <!--父子组件传值-->
     <Scroll class="vio" :on-reach-bottom="handleReachBottomsay">
        <Row v-for="(item,index) in saiy" :key='index'>
            <Col span="8" >
             <img @click="lunbotu(index)" height="120" width="95%" :src="item.header"   alt="">
            </Col>
             <Col span="16">
              <p>用户：{{item.username}}发表说说...</p>
              <textarea   cols="35" rows="5" :title="item.text">{{item.text}}</textarea>
            </Col>
         </Row>
        </Scroll>
        <Spin size="large" fix v-if="spinShow">加载中</Spin>   
       <Modal title="Title" v-model="modal10" class-name="vertical-center-modal">
           <mt-swipe :auto="4000" class="swipe">
                <mt-swipe-item v-for="(item,index) in  imguer" :key='index'>
                    <img height="300px" width="100%" :src="item.header" alt="">
                </mt-swipe-item>
            </mt-swipe>
        </Modal>     
    </div>

</template>
<script>
 import son from "./../home/son.vue"
 import common from './../../common/common.js'
export default {

   data () {
       return {
        gg : false,
        num: 1 ,
        saiy : [],
        spinShow : true,
        imguer : [],
        modal10: false,
       }
   },
   created() {
       this.getsay();
   },
   methods:{
      add () {
         this.num +=1;
         if (this.num > 10) {
         }
      },
      getsay () {
          const url = common.apihttp + '/satinGodApi?type=2&page='+this.num;
          this.$http.get(url).then((res)=>{
              this.saiy = res.body.data;
              var arr=[]
              this.imguer=arr.concat(this.saiy);
              console.log(this.imguer)
              setTimeout(()=>{
                this.spinShow = false ;
              
              },2000)
              
          })
      },
      handleReachBottomsay () {
          this.spinShow = true ;
          this.num += 1 ;
          this.getsay();
      },
      lunbotu(index){
          var add = this.saiy.slice(index,1*index+1);
         this.imguer.unshift(add[0]);
         this.modal10 = true ;    
      },
     

   },
   components:{//注册组建
          son 
   }, 
   beforeDestroy () {
        var  ss= this.imguer.shift(1,0)
         console.log(ss)
      }
}
</script>

<style>
   .ivu-scroll-container {
    height: 500px !important;
    overflow: auto;
    }
    .swipe {
    height: 250px;
    width: 100%;
    }
    #saiy p {
         text-align: left;
         padding: 5px;
    }
    .vertical-center-modal {
      display: flex;
      align-items: center;
      justify-content: center;
      }
      .ivu-modal {
          top: 0;
      }
      .mint-swipe {
          height: 250px;
         width: 100%;
       }
       .mint-swipe-indicators {
           display: none;
       }

</style>



