<template>
    <div>
       <img width="100%" height="250px" :src="stroydailDAta.cover" alt="">
        <p>作者：{{stroydailDAta.author}}</p>
        <p>小说类型：{{stroydailDAta.category}}</p>
        <p>小说名称：{{stroydailDAta.title}}</p>
        <p>标签：{{stroydailDAta.tags}}</p>
        <p>小说简介： {{stroydailDAta.desc}}</p>
        <Spin size="large" fix v-if="spinShow">加载中</Spin>
    </div>
</template>
<script>

 import common from './../../common/common.js'
export default {
   
   data () {
       return {
           stroydailDAta : [],
           spinShow : true
       }
   },
   created() {
     this.getdail(this.$route.params.idname)
   },
   mounted() {
       
   },
   methods:{
       getdail(names){       
            const url = common.apihttp+"/novelInfoApi?name="+names;
            this.$http.get(url).then((res)=>{
            this.stroydailDAta = res.body.data.aladdin;
            this.spinShow = false;
            })

       }
       
   }
}
</script>



